.icons-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color:rgb(21, 45, 53)
}

.icons-container div {
  margin: 0.3em;
}

.icons-container div a {
  text-decoration: none;
  color: inherit;
}

.icons-container div a div {
  background-color: rgb(21, 45, 53);
  color: rgb(212, 236, 221);
  padding: 0.3em;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  font-size: 4em;
}

.icons-container div a h3 {
  text-align: center;
}